.card {
  margin: 1em 0;
}

.horizontal {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.text-field {
  width: 30ch;
  margin: 0.6ch 2ch;
}

.margined-bottom {
  margin-bottom: 1em;
}

.enabled {
  opacity: 1;
  transition-property: opacity;
  transition-duration: 1s;
}

.disabled {
  pointer-events: none;
  opacity: 0.3;
  transition-property: opacity;
  transition-duration: 1s;
}